.backdrop {
  z-index: 10;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  transition: opacity 250ms var(--timing-function), visibility 250ms var(--timing-function);
  background-color: rgba(94, 99, 99, .7);
  padding-top: 60px;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

@media screen and (min-width: 480px) {
  .backdrop {
    padding: 31px;
  }
}

.modal {
  background-color: var(--yellow);
  min-height: 400px;
  opacity: 1;
  transition: transform 250ms var(--timing-function), opacity 250ms var(--timing-function);
  border-radius: 30px;
  padding: 32px 20px;
  position: relative;
  transform: scale(1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

@media screen and (min-width: 480px) {
  .modal {
    width: 100%;
    max-width: 458px;
    padding: 32px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);
  }
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transform: scale(.3);
}

@media screen and (min-width: 480px) {
  .backdrop.is-hidden .modal {
    transform: translate(-50%, -50%)scale(.3);
  }
}

.modal__close-icon {
  width: 24px;
  height: 24px;
  fill: currentColor;
  position: relative;
  top: 1px;
}

.modal__title {
  text-align: center;
