}

.form-modal__label {
  color: var(--praimery-text-color);
  margin-bottom: 2px;
  margin-left: 12px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.4;
}

.form-modal__input {
  cursor: pointer;
  font: inherit;
  height: 48px;
  width: 100%;
  transition: border 250ms var(--timing-function);
  background: #fff;
  border: 1px solid rgba(18, 18, 18, .12);
  border-radius: 30px;
  margin: 0;
  padding: 14px 0 14px 12px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, .15);
}

.form-modal__input::-ms-input-placeholder {
  color: #c4c4c4;
}

.form-modal__input::placeholder {
  color: #c4c4c4;
}

.form-modal__input:focus, .form-modal__input:not(:placeholder-shown):valid {
  border: 1px solid var(--dark-green);
}

.form-modal__input:not(:focus):not(:placeholder-shown):invalid {
  border: 1px solid var(--eror-color);
}

.form-modal__btn {
  border: 2px solid var(--dark-green);
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.form-modal:valid .form-modal__btn {
  background-color: var(--accent-green);
}

.form-modal__error {
  color: var(--eror-color);
  padding-left: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 1.4;
  display: none;
  position: absolute;
  bottom: -14px;
  left: 12px;
}

@media screen and (min-width: 768px) {
  .form-modal__error {
    font-size: 13px;
    bottom: -16px;
    left: 12px;
  }
}

.form-modal__error:before {
  content: "";
  width: 12px;
  height: 12px;
  background-image: url("error-icon.aeca23f0.svg");
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
}

.form-modal__input:not(:focus):not(:placeholder-shown):invalid + .form-modal__error {
  display: block;
}

